@keyframes slideIn {
    0% {
        transform: translateX(-10px);
    }
    50% {
        transform: translateX(10px);
    }
    100% {
        transform: translateX(0px);
    }
}

.flash {
    background: #141414;
    font-size: 1.5rem;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
    margin: 1rem 0;
    padding: 2rem;
    position: relative;
    z-index: 1;
    border-radius: 3px;
    display: flex;
    animation: slideIn forwards 0.2s ease-in-out 2;
    animation-timing-function: cubic-bezier(0.01, 1.68, 0.58, 1);
    & + .flash {
        animation-delay: 0.55s;
        & + .flash {
            animation-delay: 0.6s;
        }
    }
    &__text {
        flex: 1;
    }
    &__remove {
        background: none;
        border: 0;
        color: $text-color;
        font-size: 1.5rem;
    }
    &:after {
        content: '';
        display: block;
        background: #323232;
        position: absolute;
        width: calc(100% - 6px);
        height: calc(100% - 6px);
        top: 3px;
        left: 3px;
        z-index: -1;
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
        border-radius: 3px;
    }
    &--success {
        background: #36cc36;
    }
    &--error {
        background: #ef3737;
    }
    &--info {
        background: #3495ce;
    }
    p {
        margin: 0;
    }
}



